@import "node_modules/normalize.css-importable/normalize.scss";
@import "node_modules/fullpage.js/jquery.fullPage.scss";
@import "_grid.scss";

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
    40% {
    transform: translateY(-30px);
  }
    60% {
    transform: translateY(-15px);
  }
}

.arrow {
  position: absolute;
  bottom: 0;
  left: 50%;
  margin-left:-20px;
  width: 40px;
  height: 40px;
  background-image: url(data:image/svg+xml;base64,);
  background-size: contain;
}

.bounce {
  animation: bounce 2s infinite;
}

.text-center {
    text-align:center;
}


.section-inner {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    &.brand {
        width: 500px;
        height: 400px;
    }
    &.feature {
        width: 900px;
        height: 600px;
    }
    &.quick-start {
        width: 600px;
        height: 600px;
    }
    &.documentation {
        width: 600px;
        height: 300px;
    }
}

.brand {
    .github {
        margin-top: 30px;
    }
}

.feature {
    h2 {
        font-size: 1.1em;
        text-align: center;
    }
}

.quick-start {
    code {
        border: 1px beige solid;
        color: brown;
        padding: 8px 10px;
        display: block;
        width: 100%;
    }
}

#lang-switcher {
    position: fixed;
    top: 20px;
    left: 50%;
    width: 66px;
    text-align: center;
    margin-left: -33px;
    a {
        color: cornflowerblue;
        text-decoration: none;
    }
}